// src/components/MyComponent.tsx
"use client";
import React, { useState, useEffect } from "react";
import { onAuthStateChanged, User } from "firebase/auth";
import { auth } from "@/firebase/config";
import dynamic from "next/dynamic"; // Import dynamic
import firebase from "firebase/compat/app";

const FirebaseUIWidget = dynamic(() => import("./ui/FirebaseUIWidget"), {
  ssr: false,
}); // Dynamic import

// Import types from firebaseui if needed
import firebaseui from "firebaseui"; // Ensure this is present and correct

const MyComponent: React.FC = () => {
  const [user, setUser] = useState<User | null>(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (firebaseUser) => {
      setUser(firebaseUser || null);
    });
    return () => unsubscribe(); // Cleanup subscription
  }, []);

  const uiConfig: firebaseui.auth.Config = {
    signInFlow: "popup",
    signInSuccessUrl: "/signedIn", // Or a callback function
    signInOptions: [
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      firebase.auth.EmailAuthProvider.PROVIDER_ID,
    ],
    callbacks: {
      signInSuccessWithAuthResult: () => false, // Prevent redirect
    },
  };

  return (
    <div>
      {user ? (
        <p>Welcome, {user.email}!</p>
      ) : (
        <FirebaseUIWidget uiConfig={uiConfig} />
      )}
    </div>
  );
};

export default MyComponent;
